<template>
  <div id="payPwd">
    <mt-header title="设置支付密码" style="background: #fff;color: #1B1B1B;">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
      <!--<mt-button icon="more" slot="right"></mt-button>-->
    </mt-header>
    <div style="margin: 15px">
      <div  style="margin-left:20px;">输入支付密码</div>
      <input ref="pwd" type="tel" maxlength="6" v-model="msg" class="pwd" unselectable="on" />
      <ul class="pwd-wrap" @click="focus">
        <li :class="msg.length == 0?'psd-blink':''"><i v-if="msg.length > 0"></i></li>
        <li :class="msg.length == 1?'psd-blink':''"><i v-if="msg.length > 1"></i></li>
        <li :class="msg.length == 2?'psd-blink':''"><i v-if="msg.length > 2"></i></li>
        <li :class="msg.length == 3?'psd-blink':''"><i v-if="msg.length > 3"></i></li>
        <li :class="msg.length == 4?'psd-blink':''"><i v-if="msg.length > 4"></i></li>
        <li :class="msg.length == 5?'psd-blink':''"><i v-if="msg.length > 5"></i></li>
      </ul>
    </div>
    <div style="margin: 15px">
      <div style="margin-left:20px;">再次输入支付密码</div>
      <input ref="pwd" type="tel" maxlength="6" v-model="msg" class="pwd" unselectable="on" />
      <ul class="pwd-wrap" @click="focus">
        <li :class="msg.length == 0?'psd-blink':''"><i v-if="msg.length > 0"></i></li>
        <li :class="msg.length == 1?'psd-blink':''"><i v-if="msg.length > 1"></i></li>
        <li :class="msg.length == 2?'psd-blink':''"><i v-if="msg.length > 2"></i></li>
        <li :class="msg.length == 3?'psd-blink':''"><i v-if="msg.length > 3"></i></li>
        <li :class="msg.length == 4?'psd-blink':''"><i v-if="msg.length > 4"></i></li>
        <li :class="msg.length == 5?'psd-blink':''"><i v-if="msg.length > 5"></i></li>
      </ul>
    </div>
    <div style="text-align: center;width: 50%;margin: 20px auto">
      <el-button type="primary" style="width: 100%;" @click="sendCode">完成</el-button>
    </div>

  </div>
</template>
<script>

  export default {
    components: {},
    data() {
      return {
        msg: '',
      }
    },
    created() {},
    computed: {},
    watch: {
      msg(curVal) {
        if(/[^\d]/g.test(curVal)) {
          this.msg = this.msg.replace(/[^\d]/g, '');
        }
      },
    },
    methods: {
      focus() {
        this.$refs.pwd.focus();
      },
      sendCode() {
          console.log(this.msg)
      }
    },
    mounted() {}
  }
</script>
<style lang="less" scoped>
  #payPwd {
    height: auto;

  input[type=tel] {
    width: 0.1px;
    height: 0.1px;
    color: transparent;
    position: relative;
    top: 23px;
    background: #000000;
    left: 46px;
    border: none;
    font-size: 18px;
    opacity: 0;
    z-index: -1;
  }

    .psd-blink {
      display: inline-block;
      background: url("../../../static/img/my/bind.png") no-repeat center;
    }
  .pwd-wrap {
    width: 90%;
    height: 50px;
    padding-bottom: 1px;
    margin: 0 auto;
    background: #fff;
    border: 1px solid #ddd;
    display: flex;
    display: -webkit-box;
    display: -webkit-flex;
    cursor: pointer;

    left: 0;
    right: 0;
    top: 13%;
    z-index: 10;
  li {
    list-style-type: none;
    text-align: center;
    line-height: 50px;
    -webkit-box-flex: 1;
    flex: 1;
    -webkit-flex: 1;
    border-right: 1px solid #ddd;
  &:last-child {
     border-right: 0;
   }
  i {
    height: 10px;
    width: 10px;
    border-radius: 50%;
    background: #000;
    display: inline-block;
  }
  }
  }

  }
</style>



